.section-container {
    width: inherit;
}

/* Text section */
.text-section {
    width: 50%;
    padding: 0 32px;
}

.text-headline {
    margin-bottom: 12px;
    color: rgb(77, 75, 75);
    color: white;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: normal;
    letter-spacing: .7px;
    text-align: right;
}

.text-title {
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 32px;
    font-weight: bold;
    text-align: right;
}

.text-title-ii {
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 32px;
    font-weight: bold;
    text-align: right;
}

.text-title-sub {
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    font-weight: bold;
    text-align: right;
}

.text-descritpion {
    margin: 20px 0;
    color: white;
    color: black;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
    line-height: 1.6rem;
}

.text-appointment-btn-i {
    padding: 14px 20px;
    color: white;
    border: 1px solid transparent;
    border-radius: 28px;
    outline: transparent;
    background-color: #1A8EFD;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .8px;
    cursor: pointer;
    transition: all .4s ease;
}

.text-appointment-btn-i:hover {
    color: #1A8EFD;
    background-color: transparent;
    border: 1px solid #1A8EFD;
}

.text-stats {
    margin-top: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 48px;
}

.text-stats-container {
    text-align: center;
}

.text-stats-container>p {
    color: rgb(77, 75, 75);
    font-family: 'Montserrat', sans-serif;
    font-size: 22px;
    letter-spacing: .7px;
}

.text-stats-container p:first-child {
    margin-bottom: 8px;
    color: #1A8EFD;
    font-family: 'Montserrat', sans-serif;
    font-size: 32px;
    font-weight: bold;
}

/* Image section */
.hero-image-section {
    width: 40%;
    max-width: 100%;
    text-align: center;
}

.hero-image1 {
    width: 100%;
    height: auto;
    opacity: 0.6;
}

.hero-image2 {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0.18;
}

.hero-image3 {
    position: relative;
    width: 100%;
    opacity: 0.18;
}

/* Scroll Btn */
.scroll-up {
    width: 45px;
    height: 45px;
    display: none;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: white;
    border: 3px solid white;
    border-radius: 50%;
    background-color: #1A8EFD;
    position: fixed;
    bottom: 50px;
    right: 50px;
    font-size: 24px;
    z-index: 20;
    cursor: pointer;
}

.show-scroll {
    display: flex;
}


/* Responsive */
@media screen and (max-width: 1400px) {
    #container {
        display: none;
    }

    .hero-image3 {
        display: none;
    }

    .text-section {
        width: 100%;
        padding-left: 100px;
    }

    .hero-image-section {
        display: none;
    }
}

@media screen and (max-width: 900px) {

    .text-section {
        width: 100%;
        padding: 0;
    }

    .text-title,
    .text-descritpion {
        width: 100%;
        color: black;
        text-align: right;
    }
}

@media screen and (max-width: 600px) {
    .text-headline {
        font-size: 20px;
    }

    .text-title {
        font-size: 32px;
    }

    .text-descritpion {
        font-size: 16px;
    }

    .text-appointment-btn {
        font-size: 16px;
    }

    .text-stats {
        gap: 18px;
    }

    .text-stats-container>p {
        font-size: 16px;
    }

    .text-stats-container p:first-child {
        font-size: 22px;
    }

    .scroll-up {
        bottom: 32px;
        right: 32px;
    }

    .hero-image3 {
        display: none;
    }
}

#container {
    width: 600px;
    height: 400px;
    opacity: 0.6;
    position: relative;
    margin-bottom: 20px;
}

#circle {
    top: 300px;
    left: 420px;
    position: absolute;
    width: 100px;
    height: 100px;
    background-color: orange;
    border-radius: 100px;

}

#small-circle {
    top: 300px;
    left: 300px;
    position: absolute;
    width: 70px;
    height: 70px;
    background-color: blue;
    border-radius: 70px;


}

#smallest-circle {
    top: 240px;
    left: 340px;

    position: absolute;
    width: 40px;
    height: 40px;
    background-color: #f00;
    border-radius: 40px;
}

#smaller-circle {
    top: 280px;
    left: 370px;
    position: absolute;
    width: 50px;
    height: 50px;
    background-color: mediumslateblue;
    border-radius: 50px;

}

#reactPlayerStyle {
    padding: 50px;
}

.flowhero-section-i {
    padding: 120px 50px 50px 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
    
    background: linear-gradient(to top, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.9)), url('../../../../assets/images/mobile.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    object-fit: scale-down;
}

.flowhero-section-ii {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
    padding: 20px 0 100px;
}

/* Image */
.about-image-content-i {
    width: 50%;
    max-width: 100%;
    text-align: center;
    margin: 80px 0;
}

.about-image1 {
    width: 80%;
    height: auto;
}

/* Text */
.about-text-content {
    width: 50%;
}

.about-title {
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.about-title>span {
    position: relative;
}

.about-title span::before {
    content: '';
    position: absolute;
    width: 75%;
    height: 4px;
    background-color: #9D2553;
    border-radius: 8px;
    left: 0;
    bottom: -12px;
}

.about-description {
    margin: 32px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    letter-spacing: .7px;
    line-height: 1.7rem;
}

.about-text-title {
    margin: 0 0 24px 0;
    color: black;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: bold;
    letter-spacing: .7px;
}

/* Steps */
.about-text-step {
    margin: 16px 0;
}

.about-text-sTitle {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: .7px;
}

.about-text-description {
    margin: 8px 24px;
    color: #4d4b4b;
    color: black;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: .7px;
    line-height: 1.6rem;
}

.fa-icon {
    color: black;
}

.logoUrlImg {
    object-fit: contain;
    height: 500px;
    width: 100%;
    /* padding: 30px; */
}

/* Responsive */
@media screen and (max-width: 1000px) {
    
    .about-title {
        font-size: 32px;
        padding-bottom: 20px;
    }

    .flowhero-section-i {
        display: block;
        background: linear-gradient(to top, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.9)), url('../../../../assets/images/mobile.jpg');
        padding: 150px 50px 50px 50px;
    }

    .flowhero-section-ii {
        justify-content: left;
        align-items: left;
        display: flex;
    }

    .text-title-ii {
        text-align: right;
    }
    .about-image-content-i {
        display: none;
    }

    .about-image1 {
        width: 100%;
        height: 100%;
    }

    .about-text-content {
        width: 100%;
    }
}

